.section {
    margin-bottom: 20px;

    @include ele(divided) {
        display: flex;
        justify-content: space-between; }

    @include ele(header) {
        margin: 0;
        color: $primaryColor; }

    @include ele(indented) {
        margin-left: 15px;
        padding-top: 5px; }

    @include ele(item) {
        padding: $buttonSpace 0;
        border-bottom: $border;

        &:last-child {
            border-bottom: 0; } }

    @include medium() {

        & .button:first-child {
            margin-right: $buttonSpace; } } }
